.loader-wrapper {
  width: 100vw;
  height: 100vh;
  background-color: #cccccca2;
  position: absolute;
  z-index: 10;

  top: 0;
  left: 0;

  display: flex;
  align-items: center;
  justify-content: center;
}

.disabled {
  display: none;
}

.loader {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    display: inline-block;
    position: relative;
    border: 10px solid;
    border-color: rgba(0, 102, 255, 0.15) rgba(0, 102, 255, 0.25) rgba(0, 102, 255, 0.35) rgba(0, 102, 255, 0.5);
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
  }
  
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  } 